import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import arrowLeft from '../img/arrowLeft.png'
import tilda from '../img/tilda.png'

import geo1 from '../img/test/geo1.jpg';
import geo2 from '../img/test/geo2.png';
import geo3 from '../img/test/geo3.jpg';
import { Link } from "react-router-dom";

function Geological() {
    const settings = {
        dots: true,
        infinite: true,
        speed: 1000,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 5000,
      };
      const slider = React.useRef(null);
  return (
<section className="geological">
<div className="container">
        <Link to='/services' className="geological__title"><img src={arrowLeft} alt="" /><p></p></Link>
        <p className="geological__subtitle">    :</p>
            <div className="geological__conditions">
            <p className="geological__subtitle geological__subtitle_mobile ">    :</p>
                <ul className="geological__conditions_list">
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>       1200 .  PQ, HQ, NQ, BQ.       </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>      </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>   </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>  ,   
                        ;    </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>    ,    </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>   , , 
                       </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p> </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>  ,     </p></li>
                    <li className="geological__conditions_item"><img src={tilda} alt="tilda" /><p>     </p></li>
                    </ul>
                    <div className="geological__slider">
                        <button className="slick_prev" onClick={() => slider?.current?.slickPrev()}></button>
                            <Slider ref={slider} {...settings}>
                                <img src={geo1} alt="" className="test" />
                                <img src={geo2} alt="" className="test" />
                                <img src={geo3} alt="" className="test" />  
                            </Slider>
                        <button className="slick_next" onClick={() => slider?.current?.slickNext()}></button>        
                    </div> 
                    </div>
</div>
</section>
    );
}

export default Geological;